import { Box, Button, Stack, TextField, Typography } from "@mui/material";
import axios from "axios";
import { Form, Formik } from "formik";
import { Link } from "react-router-dom";
import * as Yup from "yup";

const RegistrationSchema = Yup.object().shape({
  firstName: Yup.string()
    .min(3, "Too short!")
    .max(20, "Too long!")
    .required("Please enter first name"),
  lastName: Yup.string()
    .min(3, "Too short!")
    .max(20, "Too long!")
    .required("Please enter last name"),
  email: Yup.string().email("Invalid email").required("Please enter email"),
  username: Yup.string()
    .min(3, "Too short!")
    .max(20, "Too long!")
    .required("Please enter first name"),
  password: Yup.string()
    .min(8, "Password must contain 8-15 characters")
    .max(15, "Password must not contain more than 15 characters")
    .required("Password is required"),
  confirmPassword: Yup.string()
    .oneOf([Yup.ref("password")], "Passwords must match")
    .required("Please confirm password"),
});

const RegisterUser = () => {
  return (
    <Formik
      validationSchema={RegistrationSchema}
      validateOnBlur
      validateOnChange={false}
      initialValues={{
        username: "",
        email: "",
        firstName: "",
        lastName: "",
        password: "",
        confirmPassword: "",
      }}
      onSubmit={(values) => {
        axios.post("https://dummyjson.com/users/add", values);
      }}
    >
      {({ errors, touched, values, handleChange, handleBlur }) => {
        return (
          <Form>
            <Stack width="100%" direction="column" spacing={3}>
              <TextField
                error={!!(errors.username && touched.username)}
                helperText={
                  Boolean(errors.username && touched.username)
                    ? errors.username
                    : null
                }
                id="username"
                label="Username"
                size="small"
                name="username"
                value={values.username}
                onChange={handleChange}
                onBlur={handleBlur}
              />
              <TextField
                error={!!(errors.email && touched.email)}
                helperText={
                  Boolean(errors.email && touched.email) && errors.email
                }
                label="email"
                size="small"
                name="email"
                value={values.email}
                onChange={handleChange}
                onBlur={handleBlur}
              />
              <Stack direction="row" spacing={2}>
                <TextField
                  fullWidth
                  error={!!(errors.firstName && touched.firstName)}
                  helperText={
                    Boolean(errors.firstName && touched.firstName) &&
                    errors.firstName
                  }
                  label="First name"
                  size="small"
                  name="firstName"
                  value={values.firstName}
                  onChange={handleChange}
                  onBlur={handleBlur}
                />
                <TextField
                  fullWidth
                  error={!!(errors.lastName && touched.lastName)}
                  helperText={
                    Boolean(errors.lastName && touched.lastName) &&
                    errors.lastName
                  }
                  label="Last name"
                  size="small"
                  name="lastName"
                  value={values.lastName}
                  onChange={handleChange}
                  onBlur={handleBlur}
                />
              </Stack>
              <TextField
                error={!!(errors.password && touched.password)}
                helperText={
                  Boolean(errors.password && touched.password) &&
                  errors.password
                }
                label="Password"
                type="password"
                size="small"
                name="password"
                value={values.password}
                onChange={handleChange}
                onBlur={handleBlur}
              />
              <TextField
                error={!!(errors.confirmPassword && touched.confirmPassword)}
                helperText={
                  Boolean(errors.confirmPassword && touched.confirmPassword) &&
                  errors.confirmPassword
                }
                label="Confirm password"
                size="small"
                name="confirmPassword"
                type="password"
                value={values.confirmPassword}
                onChange={handleChange}
                onBlur={handleBlur}
              />
              <Stack width="100%" justifyContent="center">
                <Box width="auto">
                  <Button variant="contained" type="submit">
                    Register
                  </Button>
                </Box>
              </Stack>
              <Typography>
                Already a user? <Link to="/login">Login</Link>
              </Typography>
            </Stack>
          </Form>
        );
      }}
    </Formik>
  );
};

export default RegisterUser;
